import React from 'react';
import './Track.css';



class Track extends React.Component  {

    constructor(props){
        super(props);

        this.addTrack = this.addTrack.bind(this);
        this.removeTrack = this.removeTrack.bind(this);
        this.renderUrl = this.renderUrl.bind(this);
        this.renderIframe = this.renderIframe.bind(this);
    }

    renderUrl() {
        const base = 'https://open.spotify.com/embed?uri='
        const url = `${base}${this.props.track.uri}`
        return url;
    }

    renderAction() {
        if (this.props.isRemoval) {
            return <button className="Track-action" onClick={this.removeTrack}>-</button>
        } else {
            return <button className="Track-action" onClick={this.addTrack}>+</button>
        }
    }

    renderIframe() {
        if(this.props.track.uri) {
            return <iframe title="Spotify player" src={this.renderUrl()} width="80" height="80" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe>
        }
    }

    addTrack() {
        this.props.onAdd(this.props.track);

    }

    removeTrack() {
        this.props.onRemove(this.props.track);
    }

    render () {
        return (
            <div className="Track">
             <div className="Track-information">
                <h3>{this.props.track.name}</h3>
                <p>{this.props.track.artist} |  {this.props.track.album}</p>
             </div>
             {this.renderIframe()}
             {this.renderAction()}
            </div>
        )
    }
}

export default Track;